<template>
<Intersect @enter="this.onEnter" :threshold="[windowWidth < 1024 ? 0.1 : 0.5]" @leave="this.onLeave">
  <section class="row partners">
    <h2 class="double-space blue">PARTNERS</h2>
    <div class="partner-list">
      <template v-for="item in partner">
        <a :key="item.id" :href="item.link" class="partner-item"  target="_blank">
          <img :src="'/static/partnerLogo/' + item.name + '.png'" :alt="item.name" />
        </a>
      </template>
    </div>
  </section>
</Intersect>
</template>

<script>
import { TweenMax, Power4 } from 'gsap/all'
import Intersect from 'vue-intersect'

export default {
  components: {
    Intersect
  },
  data () {
    return {
      partner: [
        {
          name: 'redbull',
          link: 'https://www.redbull.com/us-en/'
        },
        {
          name: 'hilton',
          link: 'https://www.hilton.com/en/?WT.mc_id=zlada0ww1hi2psh3ggl4advcrb5dkt6multibr7_153670571_1003528&gclid=EAIaIQobChMI7YmYy67b5QIVfh6tBh2c_QvVEAAYASAAEgKQefD_BwE&gclsrc=aw.ds'
        },
        {
          name: 'millerlite',
          link: 'https://www.millerlite.com/av?url=https://www.millerlite.com/?gclid=EAIaIQobChMIoIDN2a7b5QIVRCCtBh3YuQHTEAAYASAAEgJLA_D_BwE'
        },
        {
          name: 'bmwgroupdesignworks',
          link: 'https://www.bmwgroupdesignworks.com/'
        },
        {
          name: 'foxmovies',
          link: 'https://www.foxmovies.com/'
        },
        {
          name: 'sony',
          link: 'https://www.sony.com/'
        },
        {
          name: 'jcdecaux',
          link: 'https://www.jcdecaux.com/'
        },
        {
          name: 'clearchanneloutdoor',
          link: 'http://clearchanneloutdoor.com/'
        },
        {
          name: 'warnerbros',
          link: 'https://www.warnerbros.com/'
        },
        {
          name: 'paramount',
          link: 'https://www.paramount.com/'
        }
      ]
    }
  },
  computed: {
    windowWidth () {
      return window.innerWidth
    }
  },
  methods: {
    onEnter () {
      // this.$el.style.visibility = 'visible'
      TweenMax.to(this.$el.querySelector('h2'), 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut })
      TweenMax.staggerTo(this.$el.querySelectorAll('div'), 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut }, 0.1)
    },
    onLeave () {
      // this.$el.style.visibility = 'hidden'
    }
  },
  mounted () {
    TweenMax.set(this.$el.querySelector('h2'), { y: 30, opacity: 0 })
    TweenMax.set(this.$el.querySelectorAll('div'), { y: 30, opacity: 0 })
  }
}
</script>

<style lang="scss">
.partners {
  position: relative;
  padding-top: 110px;
  padding-bottom: 103px;
  &:before {
    content: "";
    background: $transparentBlack;
    position: absolute;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 50%;
    margin-left: -50vw;
  }
  @include media-query(lg) {
    padding-top: 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    min-height: 900px;
  }
  h2 {
      margin-bottom: 80px;
  }
  .partner-list {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;
  }
  .partner-item {
      width: 20%;
      margin: 40px auto;
      padding: 0 10px;

      @include media-query(lg) {
        padding: 0 15px;
      }

      img {
        width: 100%;
        max-width: 150px;
      }
  }
}
</style>



