<template>
  <nav class="navigation" :class="{ disabled: this.disabled }">
    <ul class="bullets">
      <li v-for="i in 7" :key="`bullet${i}`" @click="$emit('clickBullet', i)" :class="{ selected: current === i - 1 }">
      </li>
    </ul>
  </nav>
</template>

<script>
import { TweenMax, Power4 } from 'gsap/all'
export default {
  props: {
    current: {
      type: Number,
      default: 0
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    TweenMax.staggerFromTo(this.$el.querySelectorAll('ul li'), 2, { y: 10, opacity: 0 }, { y: 0, opacity: 1, ease: Power4.easeInOut }, 0.1)
  }
}
</script>

<style lang="scss">
nav.navigation {
  position: fixed;
  right: 10px;
  display: none;
  transition: opacity 0.3s linear;
  &.disabled {
    pointer-events: none;
    opacity: 0;
  }
  @include media-query(md) {
    right: 20px;
  }
  @include media-query(lg) {
    right: 40px;
    display: block;
  }
  @include media-query(xlg) {
    right: 60px;
  }
  height: 100%;
  min-height: 870px;
  ul.bullets {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    li {
      display: block;
      width: 14px;
      height: 14px;
      margin-bottom: 14px;
      border-radius: 50%;
      border: 1px solid #fff;
      cursor: pointer;
      position: relative;
      box-sizing: border-box;
      &:last-child {
        margin-bottom: 0;
      }
      &:after {
        content: "";
        position: absolute;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        background: $white;
        top: -1px;
        left: -1px;
        box-sizing: border-box;
        transition: all 1s $easeOutQuint;
        opacity: 0;
        transform: scale(0.5, 0.5);
      }
      &.selected {
        &:after {
          opacity: 1;
          transform: scale(1,1);
          pointer-events: none;
        }
      }
    }
  }
}
</style>



