<template>
  <div class="preloader">
    <div class="preloader-bar" ref="bar" />
    <h1 ref="logo">Geenee</h1>
    <div class="black-over" ref="black" />
  </div>
</template>

<script>
import { TweenMax, Power4 } from 'gsap/all'
import '@/vendors/PreloadJs'

export default {
  mounted () {
    TweenMax.from(this.$refs.logo, 2, { scaleX: 0.9, scaleY: 0.9, opacity: 0, ease: Power4.easeOut, force3D: true })
    TweenMax.to(this.$refs.black, 2, { y: -150, ease: Power4.easeOut, force3D: true, onComplete: this.iniAnimationDone })
    this.startLoading()
  },
  methods: {
    startLoading () {
      // eslint-disable-next-line no-undef
      this.queue = new createjs.LoadQueue(true)
      this.queue.loadFile('/static/images/bg-space.jpg', { id: 'bg' })
      if (window.innerWidth >= 1024) {
        this.queue.loadFile('/static/images/globe@2x.png', { id: 'globe' })
        this.queue.loadFile('/static/images/halo@2x.png', { id: 'halo' })
      } else {
        this.queue.loadFile('/static/images/globe.png', { id: 'globe' })
        this.queue.loadFile('/static/images/halo.png', { id: 'halo' })
      }
      this.queue.loadFile('/static/images/video.gif', { id: 'loop' })
      this.queue.loadFile('/static/images/gradient.png', { id: 'gradient' })
      this.queue.loadFile('/static/images/andrew.jpg', { id: 'andrew-rogal' })
      this.queue.loadFile('/static/images/alex.jpg', { id: 'alex' })
      this.queue.loadFile('/static/images/luke.jpg', { id: 'luke-peterson' })
      this.queue.loadFile('/static/images/philipp.jpg', { id: 'philipp-holzschneider' })
      this.queue.loadFile('/static/images/sandro.jpg', { id: 'sandro-christen' })
      this.queue.loadFile('/static/images/sturgis.jpg', { id: 'sturgis-adams' })
      this.queue.on('progress', (evt) => {
        const cb = evt.progress === 1 ? this.endPreloader : null
        TweenMax.to(this.$refs.bar, 2, { scaleX: evt.progress, ease: Power4.easeOut, onComplete: cb })
      })
      this.queue.addEventListener('')
      this.queue.load()
    },
    iniAnimationDone () {
      this.iniAnimation = true
      if (this.end) {
        this.animateOut()
      }
    },
    endPreloader () {
      this.end = true
      if (this.iniAnimation) {
        this.animateOut()
      }
    },
    animateOut () {
      TweenMax.to(this.$refs.bar, 0.5, {
        scaleY: 0,
        ease: Power4.easeOut,
        onComplete: () => {
          window.logoRect = this.$refs.logo.getBoundingClientRect()
          this.$emit('loadComplete')
        }
      })
    }
  }
}
</script>

<style lang="scss">
div.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  div.preloader-bar {
    position: absolute;
    top: 0px;
    left: 0;
    width: 100%;
    height: 3px;
    background: $white;
    transform-origin: top left;
    transform: scale(0, 1);
  }
  h1, div.black-over {
    font-size: 0;
    width: 159px;
    height: 25px;
    background: url('/static/images/logo.png') center center no-repeat;
    background-size: cover;
    position: fixed;
    top: 50%;
    left: 50%;
    $logoWidth: 110px;
    width: $logoWidth;
    height: $logoWidth * 0.15;
    margin-left: -($logoWidth / 2);
    margin-top: -$logoWidth * 0.075;
    @include media-query(lg) {
      width: 159px;
      height: 25px;
      margin-left: -80px;
      margin-top: -12px;
    }
  }
  div.black-over {
    height: 150px;
    width: 200px;
    margin-left: -100px;
    background: linear-gradient(to bottom, rgba(0,0,0,1) 0%,rgba(0,0,0,1) 50%,rgba(0,0,0,0) 100%);
  }
}
</style>



