<template>
  <main id="home">
    <div class="scroll-height" :style="{  height: `${this.contentHeight}px` }" />
    <div class="scroll-content" ref="content">
      <transition name="fade-section" :duration="{ enter: 500, leave: 500}" mode="out-in">
        <div class="sections" v-if="$route.path !== '/privacy-policy/'" :key="$route.path">
          <Landing @endAnim="onEndAnim" />
          <About />
          <Product
            sectionTitle = 'TECHNOLOGY'
            sectionSubTitle = 'Robust, flexible, scaleable and easy to use  comprehensive tools to ensure your experience is result driven.'
            contactButton = 'ACCESS THE API'
            itemTitle1 = 'IMAGE RECOGNITION'
            itemTitle2 = 'web AUGMENTED REALITY'
            itemTitle3 = 'CONTENT TOOLS'
            itemSubTitle1 = 'Patented image recognition technology that reads any 2D graphic from video imagery to product packaging.'
            itemSubTitle2 = 'Track media position and rotation in 3D space.'
            itemSubTitle3 = 'A suite of templatized toolkits for common use cases to ensure the experience is result driven.'
          />
          <Product
            sectionTitle = 'SERVICES'
            sectionSubTitle = 'From conception to technical execution, we will help ensure you create something magical.'
            contactButton = 'GET A QUOTE'
            itemTitle1 = ''
            itemTitle2 = ''
            itemTitle3 = ''
            itemSubTitle1 = 'UX <br/> UI'
            itemSubTitle2 = '3D OBJECT DESIGN <br/> 2D & 3D ANIMATION'
            itemSubTitle3 = 'CUSTOM DEVELOPMENT <br/> TECHNICAL CONSULTING'
          />
          <Team />
          <Partners />
          <Press />
        </div>
        <PrivacyPolicy v-else  :key="$route.path"/>
      </transition>
      <footer class="row footer">
        <div class="menu">
          <a href="https://www.geeneecareers.me/" target="_blank">JOBS</a>
          <div class="spacer" />
          <router-link to="/privacy-policy/">PRIVACY POLICY</router-link>
        </div>
        © 2019. All rights reserved.
      </footer>
    </div>
    <Navigation @clickBullet="onClickBullet" :current="this.mve" v-if="this.built" :disabled="$route.path === '/privacy-policy/'" />
    <Social />
    <div class="gradient-hover" ref="gradient" />
  </main>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import { TweenMax, Power4, RoundPropsPlugin, Back } from 'gsap/all'
import Landing from './landing/Landing'
import Product from './product/Product'
import About from './about/About'
import Team from './team/Team'
import Partners from './partners/Partners'
import Press from './latestPress/Press'
import Navigation from '@/components/navigation/Navigation'
import Social from '@/components/social/Social'
import PrivacyPolicy from './privacy-policy/PrivacyPolicy'

export default {
  data () {
    return {
      contentWidth: 0,
      contentHeight: 0,
      mve: 0,
      built: false
    }
  },
  components: {
    Landing,
    Product,
    About,
    Team,
    Navigation,
    Social,
    PrivacyPolicy,
    Partners,
    Press
  },
  methods: {
    resizeContent () {
      const rect = this.$refs.content.getBoundingClientRect()
      this.contentWidth = rect.width
      this.contentHeight = rect.height
    },
    onEndAnim () {
      this.built = true
      this.resizeContent()
    },
    positeContent () {
      if (window.innerWidth < 1024) {
        return
      }
      const finalPos = window.scrollY || window.scrollYOffset
      let duration = window.innerWidth < 1024 ? 0.5 : 1
      if (this.nextScrollFast) {
        duration = 0
        this.nextScrollFast = false
      }
      TweenMax.to(this.$refs.content, duration, {
        y: -finalPos,
        ease: Power4.easeOut,
        roundProps: ['y'],
        force3D: true,
        onUpdate: () => {
          this.updateMve()
        }
      })
    },
    onScrollWindow () {
      const opacity = Math.max(0, Math.min(1, (window.scrollY || window.scrollYOffset || 0) / 100))
      TweenMax.to(this.$refs.gradient, this.nextScrollFast ? 0 : 1, { opacity, ease: Power4.easeOut })
      this.positeContent()
    },
    onClickBullet (index) {
      const rect = this.$refs.content.querySelectorAll('section')[index - 1].getBoundingClientRect()
      const posY = window.scrollY || window.scrollYOffset || 0
      TweenMax.to(window, 1, { scrollTo: { y: rect.top + posY, autoKill: false }, ease: Power4.easeOut })
    },
    updateMve () {
      const index = this.getMostVisible(this.$refs.content.querySelectorAll('section')).index
      if (index !== this.mve) {
        this.mve = index
      }
    },
    getMostVisible (elements) {
      const viewportHeight = window.innerHeight
      let max = 0
      let mve = null
      let indexSelected = 0

      elements.forEach((element, index) => {
        var visiblePx = this.getVisibleHeightPx(element, viewportHeight)
        if (visiblePx > max) {
          max = visiblePx
          mve = element
          indexSelected = index
        }
      })
      return { mve, index: indexSelected }
    },
    getVisibleHeightPx (element, viewportHeight) {
      const rect = element.getBoundingClientRect()
      const height = rect.bottom - rect.top
      let visible = { top: rect.top >= 0 && rect.top < viewportHeight, bottom: rect.bottom > 0 && rect.bottom < viewportHeight }
      let visiblePx = 0

      if (visible.top && visible.bottom) {
        // Whole element is visible
        visiblePx = height
      } else if (visible.top) {
        visiblePx = viewportHeight - rect.top
      } else if (visible.bottom) {
        visiblePx = rect.bottom
      } else if (height > viewportHeight && rect.top < 0) {
        const absTop = Math.abs(rect.top)
        if (absTop < height) {
          // Part of the element is visible
          visiblePx = height - absTop
        }
      }
      return visiblePx
    }
  },
  mounted () {
    this.positeContent()
    window.addEventListener('resize', this.resizeContent)
    window.addEventListener('scroll', this.onScrollWindow)
  },
  watch: {
    $route (from, to) {
      if (from.path !== to.path) {
        this.nextScrollFast = true
      }
    }
  },
  beforeMount () {
    window.home = this
  },
  updated () {
    this.resizeContent()
  }
}
</script>

<style lang="scss">
#home {
  max-width: 1920px;
  margin: 0 auto;
  position: absolute;
  height: 100vh;
  width: 100%;
  div.scroll-content {
    top: 0;
    left: 0;
    width: 100%;
    @include media-query(lg) {
      position: fixed;
    }
  }
  div.scroll-height {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
  }
  footer {
    padding: 43px 0px;
    padding-bottom: 150px;
    font-size: 10px;
    background: $transparentBlack;

    div.menu {
      display: flex;
      flex-direction: row;
      width: 100%;
      justify-content: center;
    }
    a {
      margin-bottom: 30px;
      display: block;
      font-size: 14px;
      color: #fff;
      position: relative;
      letter-spacing: 2px;
      &:after {
        content: "";
        width: 100%;
        height: 1px;
        background: $white;
        display: block;
        position: absolute;
        bottom: 0;
        margin-bottom: -3px;
        transition: transform 0.5s $easeOutQuint;
        transform-origin: top right;
        transform: scale(0, 1);
        margin-left: -1px;
      }
      &:hover, &.router-link-active {
        &:after {
          transform-origin: top left;
          transform: scale(1, 1);
        }
      }
      &.router-link-active {
        pointer-events: none;
      }
    }

    div.spacer {
      width: 1px;
      height: 14px;
      background: $white;
      display: block;
      margin: 0 30px;
    }
    @include media-query(lg) {
      padding: 43px 0px;
      padding-bottom: 43px;
    }
  }
  div.gradient-hover {
    width: 100vw;
    height: 150px;
    position: fixed;
    background: linear-gradient(to bottom, rgba(0,0,0,1) 50%,rgba(0,0,0,0) 100%);
    pointer-events: none;
    opacity: 0;
    top: 0;
    @include media-query(sm) {
      height: 100px;
    }
    @include media-query(lg) {
      background: linear-gradient(to bottom, rgba(0,0,0,1) 30%,rgba(0,0,0,0) 90%);
      height: 300px;
    }
  }
}
</style>



