<template>
  <nav class="social">
    <div class="gradient-social" ref="gradient" />
    <ul class="buttons">
      <li v-for="item in ['facebook', 'vimeo', 'instagram', 'medium', 'email']" :key="item">
        <template v-if="(item == 'medium') || (item == 'email')">
          <a :href="links[item]" target="_blank"><img :src="getIconForItem(item)" /></a>
        </template>
        <template v-else>
          <a :href="links[item]" v-html="getIconForItem(item)" target="_blank" />
        </template>
      </li>
    </ul>
  </nav>
</template>

<script>
import { TweenMax, Power4 } from 'gsap/all'
import facebook from './facebook.svg'
import instagram from './instagram.svg'
import vimeo from './vimeo.svg'
import email from './email-icon.png'
import medium from './medium-icon.png'

export default {
  data () {
    return {
      links: {
        facebook: 'https://www.facebook.com/geenee.me',
        vimeo: 'https://vimeo.com/258155116',
        instagram: 'https://www.instagram.com/geenee/',
        medium: '',
        email: ''
      }
    }
  },
  computed: {
    vimeoSvg () {
      return vimeo
    },
    instagramSvg () {
      return instagram
    },
    facebookSvg () {
      return facebook
    },
    mediumSvg () {
      return medium
    },
    emailSvg () {
      return email
    }
  },
  methods: {
    getIconForItem (item) {
      return this[`${item}Svg`]
    }
  },
  mounted () {
    TweenMax.staggerFromTo(this.$el.querySelectorAll('ul li'), 2, { y: 10, opacity: 0 }, { y: 0, opacity: 1, ease: Power4.easeInOut }, 0.1)
  }
}
</script>

<style lang="scss">
nav.social {
  position: fixed;
  left: 0px;
  bottom: 10px;
  @include media-query(md) {
    left: 16px;
  }
  @include media-query(lg) {
    left: 36px;
    bottom: auto;
    height: 100%;
    min-height: 870px;
  }
  @include media-query(xlg) {
    left: 56px;
  }
  div.gradient-social {
    width: 140px;
    height: 150px;
    position: fixed;
    background: linear-gradient(to bottom, rgba(0,0,0,0) 20%,rgba(0,0,0,1) 100%);
    pointer-events: none;
    bottom: 0;
    left: 0;
    filter: blur(5px);
    @include media-query(lg) {
      display: none;
    }
  }
  ul.buttons {
    display: flex;
    flex-direction: row;
    height: 100%;
    justify-content: center;
    @include media-query(lg) {
      flex-direction: column;
    }
    li {
      display: block;
      width: 40px;
      height: 40px;
      margin-bottom: 10px;
      cursor: pointer;
      position: relative;
      box-sizing: border-box;
      &:last-child {
        margin-bottom: 0;
      }
      a {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 10px;
        left: 10px;
        svg {
          width: 100%;
          height: 100%;
          path {
            fill: $white;
            transition: all 0.3s $easeOutQuint;
          }
        }

        &:hover {
          svg path {
            fill: $blue;
          }
        }
      }
    }
  }
}
</style>



