<template>
  <Intersect @enter="this.onEnter" :threshold="[0.5]" @leave="this.onLeave">
    <section class="row about">
      <div class="col-lg-12 col-lg-offset-0-half col-md-12 col-md-offset-0 col-sm-12 col-text">
      <p class="headline">Connect with your audience through the spatial web.</p>
      <p>Cost effective, fast to build, best in class technology and specialized services to transform your brand experience.</p>
      <p>Create revenue where it’s never existed before.</p>
      <a href="mailto:contact@geenee.me" class="contact">contact us to know more</a>
      </div>
    </section>
  </Intersect>
</template>

<script>
import { TweenMax, Power4 } from 'gsap/all'
import Intersect from 'vue-intersect'

export default {
  components: {
    Intersect
  },
  methods: {
    onEnter () {
      // this.$el.style.visibility = 'visible'
      TweenMax.staggerTo(this.$el.children[0].children, 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut }, 0.2)
    },
    onLeave () {
      // this.$el.style.visibility = 'hidden'
    }
  },
  mounted () {
    TweenMax.set(this.$el.children[0].children, { opacity: 0, y: 30 })
  }
}
</script>

<style lang="scss">
.about {
  align-items: center;
  display: flex;
  flex-direction: row;
  padding-top: 140px;
  padding-bottom: 20px;
  position: relative;

  &:before {
    content: "";
    background: $transparentBlack;
    position: absolute;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 50%;
    margin-left: -50vw;
  }
  @include media-query(sm) {
    min-height: 650px;
  }
  @include media-query(md) {
    height: 100vh;
    min-height: 800px;
    padding-top: 125px;
    padding-bottom: 103px;
  }
  p {
    line-height: 27px;
    font-size: 25px;
    margin: 0 0 30px;
    @include media-query(lg) {
      line-height: 47.5px;
    }
  }
  p.headline {
    color: $blue;
    font-weight: 700;
  }
  .col-text {
    max-width: 890px;
    margin: 0 auto;
  }
  a.contact {
    flex-grow: 0;
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.35px;
    padding: 17px 0px;
    border: 1px solid $white;
    position: relative;
    display: block;
    width: 320px;
    margin: 0 auto;
    margin-top: 70px;
    transition: all 2s $easeOutQuint;
    @include media-query(sm) {
      font-size: 12px;
      width: 260px;
    }
    @include media-query(md) {
      width: 350px;
    }
    &:before {
      position: absolute;
      width: 100%;
      height: 100%;
      background: $blue;
      z-index: -1;
      content: "";
      top: 0;
      left: 0;
      transform: scale(1, 1);
      opacity: 0;
      transition: all 2s $easeOutQuint;
    }
    &:hover {
      letter-spacing: 2px;
      color: $white;
      &:before {
        opacity: 0.5;
        transform: scale(1, 1);
      }
    }
  }
}
</style>



