<template>
  <section class="row privacy-policy">
    <h2>Privacy Policy</h2>
    <p>Geenee GmbH and Geenee Inc. take protection of your personal data very seriously. We treat your personal data confidentially and in accordance with legal data protection regulations. This Privacy Policy explains how we collect and store data when you make use of our website provided by Geenee GmbH and Geenee Inc.(“Geenee, “we”, “our”, “us”).</p>
    <p>The use of our website is possible without giving any personal data. If personal data (eg. name, address or e-mail addresses) are collected on our page, this is always done on a voluntary basis as far as possible. This data will not be passed on to third parties without your consent.</p>
    <p>We would like to point out that data transmission over the internet (eg. when communicating via e-mail) can have security gaps. A complete protection of the data before accessed by third parties is unfortunately not possible.</p>
    <p>If you should not consent to this privacy policy, please do not use our web services.</p>

    <h3>Cookies</h3>
    <p>The Internet pages partly use so-called cookies. Cookies do not damage your computer and contain no viruses. Cookies serve to make our offer more user-friendly, effective and safer. Cookies are small text files which are stored on your computer and which your browser saves.</p>
    <p>Most of the cookies we use are so called “session cookies”. They are automatically deleted after your visit. Other cookies remain on your device until you delete them. These cookies allow us to recognize your browser on the next visit.</p>
    <p>You can set your browser to inform you about setting cookies and to allow cookies only in individual cases, to exclude the acceptance of cookies for specific cases or in general, and to activate the automatic deletion of cookies when the browser is closed. The functionality of this website may be restricted when cookies are deactivated.</p>

    <h3>Server-Log-Files</h3>
    <p>The provider of the pages automatically collects and saves information in so-called server log files, which your browser automatically transmits to us. These are:</p>
    <ul>
      <li>Browsing type / Browserversion</li>
      <li>Operating system</li>
      <li>Referrer URL</li>
      <li>Hostname of the accessing computer</li>
      <li>Time of server request</li>
    </ul>
    <p>These data can not be attributed to specific persons. This data is not merged with other data sources. We reserve the right to review these data at a later date, if concrete indications of unlawful use become known to us.</p>

    <h3>Privacy Policy for using Google Analytics</h3>
    <p>This website uses features of the web analytics service tool Google Analytics. The provider is Google Inc., 1600 Amphitheater Parkway Mountain View, CA 94043, USA.</p>
    <p>Google Analytics uses so-called “cookies”. These are text files that are stored on your computer and allow you to analyze the use of the website. The information generated by the cookie, about your use of this website, is generally transmitted to a Google server in the USA and stored there.</p>
    <p>For more information about how to deal with Google Analytics user data, please visit Google’s privacy policy: <a href="https://support.google.com/analytics/answer/6004245?hl=en" target="_blank">https://support.google.com/analytics/answer/6004245?hl=en</a></p>

    <h4>Browser plugin</h4>
    <p>You can prevent the storage of your cookies by adjusting the settings of your browser software; However, we would like to point out that in this case, you may not be able to fully utilize all the functions of our website. In addition, you can prevent Google from collecting and processing the data (including your IP address) related to your use of the website (including your IP address), as well as the processing of this data by Google, by downloading the browser plug-in available under the following link and install: <a href="https://tools.google.com/dlpage/gaoptout?hl=en" target="_blank"> https://tools.google.com/dlpage/gaoptout?hl=en</a></p>

    <h4>Contradiction against data collection</h4>
    <p>You can prevent the collection of your data by Google Analytics by clicking on the following link: <a href="https://tools.google.com/dlpage/gaoptout?hl=en" target="_blank">https://tools.google.com/dlpage/gaoptout?hl=en</a> and opt-out the cookie settings that prevents the collection of your data on future visits to this web page.</p>

    <h4>IP anonymization</h4>
    <p>We use the “Activation of IP anonymization” function on this website. However, this will reduce your Google IP address previously within Member States of the European Union or in other States Parties to the Agreement on the European Economic Area. Only in exceptional cases will the full IP address be transferred to a Google server in the US and abbreviated there. On behalf of the operator of this website, Google will use this information to evaluate the use of the website, to compile reports on website activity, and to provide other services related to the use of the website.</p>

    <h3>Contact Form</h3>
    <p>If you send us inquiries by means of a contact form, your information from the inquiry form, including the contact data you provide there, will be saved by us for the purpose of processing the inquiry and to follow-up on you personally. We do not pass on this data to third parties without your consent.</p>

    <h3>Data protection for the use of Facebook plugins (like button)</h3>
    <p>On our pages, plugins from the social network Facebook, provider Facebook Inc., 1 Hacker Way, Menlo Park, California 94025, USA, are integrated. You can recognize the Facebook plugins on the Facebook logo or the “Like-Button” on our page. An overview of the Facebook plugins can be found here: <a href="http://developers.facebook.com/docs/plugins/" target="_blank">http://developers.facebook.com/docs/plugins/</a>.</p>
    <p>When you visit our pages, the plugin creates a direct connection between your browser and the Facebook server. Facebook will be informed that you have visited our site with your IP address. If you click the Facebook “Like button” while you are logged in to your Facebook account, you can link the content of our pages to your Facebook profile. This allows Facebook to associate the visit of our pages with your user account. We would like to point out that as a provider of the pages we do not receive any knowledge of the content of the transmitted data as well as their use by Facebook. For more information, see Facebook’s Privacy Statement at <a href="https://en-de.facebook.com/policy.php" target="_blank">https://en-de.facebook.com/policy.php</a>.</p>
    <p>If you do not want Facebook to associate the visit of our pages with your Facebook user account, please log out of your Facebook user account.</p>

    <h3>Privacy Policy for use of Instagram</h3>
    <p>On our pages, functions of the Instagram service are integrated. These functions are offered by Instagram Inc., 1601 Willow Road, Menlo Park, CA, 94025, USA. If you are logged into your Instagram account, you can link the contents of our pages to your Instagram profile by clicking on the Instagram button. This allows Instagram to associate the visit of our pages with your user account. We would like to point out that as a provider of the pages we are not aware of the content of the transmitted data as well as their use by Instagram.</p>
    <p>For more information, see the Instagram Privacy Statement: <a href="http://instagram.com/about/legal/privacy/" target="_blank">http://instagram.com/about/legal/privacy/</a></p>

    <h3>Privacy Policy for using YouTube</h3>
    <p>Our website uses plugins from Google’s YouTube site. The site is operated by YouTube, LLC, 901 Cherry Ave., San Bruno, CA 94066, USA. If you visit one of our YouTube-enabled sites, you will be connected to the YouTube servers. The Youtube server is informed which of our pages you have visited.</p>
    <p>When you are logged into your YouTube account, you enable YouTube to map your surfing behavior directly to your personal profile. You can prevent this by logging out of your YouTube account.</p>
    <p>For more information about how to deal with user data, see the YouTube Privacy Policy at <a href="https://www.google.com/intl/en/policies/privacy" target="_blank">https://www.google.com/intl/en/policies/privacy</a></p>

    <h3>Privacy Policy for use of Vimeo</h3>
    <p>On our website are plugins of the video portal Vimeo of the Vimeo, LLC, 555 West 18th Street, New York, New York 10011, USA. Each time a page that offers one or more Vimeo video clips, a direct connection is made between your browser and a Vimeo server in the US. Information about your visit and your IP address is stored there.</p>
    <p>Through interactions with the Vimeo plugins (eg. clicking on the start button), this information will be also transmitted to Vimeo and stored there. If you have a Vimeo user account and do not wish Vimeo to collect your member data through this website you must log out to Vimeo before you visit this website.</p>
    <p>The privacy policy for Vimeo with further information on the collection and use of your data by Vimeo is available on <a href="https://www.vimeo.com/privacy" target="_blank">https://www.vimeo.com/privacy</a>.</p>
    <p>In addition, Vimeo tracks the Google Analytics tracker through an iFrame where the video is mentioned. This is our own tracking of Vimeo, which we do not have access to. You can block Google Analytics tracking by using the disabling tools Google provides for some internet browsers. Users may also prevent Google from tracking the Google Analytics generated data (including your IP address) related to its use of the Website, as well as the processing of such data by Google by using the browser plug-in available under the following link Download and install: <a href="https://tools.google.com/dlpage/gaoptout?hl=en" target="_blank">https://tools.google.com/dlpage/gaoptout?hl=en</a></p>

    <h3>Information, Deletion, Blocking</h3>
    <p>You’re entitled to receive free information about your stored personal data, its origin, and recipient, the purpose of the data processing and a right to correct, block or delete this data. To this end, as well as to further questions regarding the subject of personal data, you can contact us at any time at the address given in the imprint.</p>

    <p class="tagline">Last update: 1st of September 2017</p>
  </section>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import { TweenMax, Power4, RoundPropsPlugin, Back } from 'gsap/all'

export default {
  mounted () {
    TweenMax.staggerFromTo(this.$el.children, 1.3, { y: 30, opacity: 0 }, { y: 0, opacity: 1, ease: Power4.easeInOut, force3D: true, delay: !window.homeIsAlreadyBuilt ? 1.5 : 0 }, 0.05)
    window.homeIsAlreadyBuilt = true
    window.home.resizeContent()
    TweenMax.set(window, { scrollTo: { y: 0, autoKill: false }, ease: Power4.easeOut })
  }
}
</script>

<style lang="scss">
section.privacy-policy {
  padding-top: 100px;
  position: relative;
  width: 100%;
  @include media-query(lg) {
    padding-top: 180px;
  }
  &:before {
    content: "";
    background: $transparentBlack;
    position: absolute;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 50%;
    margin-left: -50vw;
    z-index: -1;
  }
  h2, p, h3, h4, ul {
    width: 100%;
  }
  h2 {
    margin-bottom: 70px;
  }
  p, h3 {
    letter-spacing: 0.5px;
  }
  p {
    font-size: 14px;
    text-align: left;
    line-height: 1.5;
    margin-bottom: 27px;
    width: 100%;
    overflow-wrap: break-word;
    word-wrap: break-word;
    @include media-query(lg) {
      font-size: 16px;
    }
  }
  h3 {
    margin-top: 80px;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 19px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    text-align: left;
    @include media-query(lg) {
      text-align: center;
    }
  }
  h4 {
    font-size: 16px;
    font-weight: 700;
    text-align: left;
    margin-bottom: 20px;
    margin-top: 40px;
  }

  p.tagline {
    font-size: 15px;
    font-style: italic;
    opacity: 0.8;
  }
  ul {
    text-align: left;
    margin-bottom: 27px;

    li {
      margin-bottom: 10px;
      list-style: disc;
      list-style-position: inside;
    }
  }
  a {
    color: $white;
    text-decoration: underline;
    transition: all 0.3s linear;
    &:hover {
      color: $blue;
    }
  }
}
</style>



