<template>
  <section class="row landing">
    <div class="background-gradient" />
    <div class="col-lg-5 image-txt col-md-12" ref="image">
      <h2>Image Recognition.</h2>
    </div>
    <Phone ref="phone" />
    <div class="col-lg-5 everywhere-txt col-md-12">
      <h2 class="double-space" ref="everywhere">WEB AUGMENTED REALITY.</h2>
    </div>
    <div class="scroll-down" ref="scrollDown">
      <span>scroll down</span>
      <div class="arrow">
        <div class="center" />
        <div class="left" />
        <div class="right" />
      </div>
    </div>
  </section>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import { TweenMax, Power4, RoundPropsPlugin, Back, Linear } from 'gsap/all'
import SplitText from '@/vendors/SplitText'
import Phone from './Phone'

export default {
  components: {
    Phone
  },
  mounted () {
    TweenMax.set(window, { scrollTo: { y: 0, autoKill: false }, ease: Power4.easeOut })
    if (window.homeIsAlreadyBuilt) {
      this.$emit('endAnim')
      return
    }
    TweenMax.fromTo(this.$refs.image, 1, { opacity: 0 }, { opacity: 1, ease: Linear.easeNone, delay: 3 })
    TweenMax.fromTo(this.$refs.phone.$el, 2, { opacity: 0, y: 20 }, { opacity: 1, y: 0, force3D: true, ease: Power4.easeOut, delay: 4 })
    TweenMax.fromTo(this.$refs.phone.$refs.screen, 0.5, { opacity: 0 }, { opacity: 0.6, y: 0, force3D: true, ease: Power4.easeOut, delay: 5 })
    if (window.innerWidth > 1053) {
      this.split = new SplitText(this.$refs.everywhere, { type: 'chars' })
      TweenMax.staggerFromTo(this.$refs.everywhere.children, 1.5, { opacity: 0 }, { opacity: 1, ease: Linear.easeNone, delay: 5 }, 0.05, () => { if (this.split) { this.split.revert() } })
    } else {
      TweenMax.fromTo(this.$refs.everywhere, 1.5, { opacity: 0 }, { opacity: 1, ease: Linear.easeNone, delay: 5 })
    }
    TweenMax.fromTo(this.$refs.scrollDown, 1,
      {
        y: 10,
        opacity: 0
      },
      {
        y: 0,
        opacity: 1,
        delay: 5.5,
        onStart: () => {
          this.$emit('endAnim')
          document.body.style.overflowY = 'scroll'
        }
      })

    window.homeIsAlreadyBuilt = true
  }
}
</script>

<style lang="scss">
.landing {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding-top: 180px;
  padding-bottom: 175px;
  height: 100vh;
  box-sizing: border-box;
  @include media-query(sm) {
   padding-top: 100px;
  }
  @media screen and (min-height: 580px) {
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 185px;
  }
  @include media-query(lg) {
    flex-direction: row;
    height: 100vh;
    min-height: 780px;
    padding-top: 0;
    padding-bottom: 0px;
    max-height: none;
  }
  .image-txt {
    text-align: center;
    font-size: 22px;
    margin-bottom: 27px;
    @media screen and (min-height: 490px) {
      margin-bottom: 38px;
    }
    @include media-query(md) {
      padding-right: 0px;
    }
    @include media-query(lg) {
      padding-right: 44px;
      text-align: right;
      margin-bottom: 0px;
    }
    @include media-query(xlg) {
      padding-right: 48px;
    }
    @media screen and (max-width: 1288px) and (min-width: 1147px) {
      h2 {
        font-size: 22px;
      }
    }
    @media screen and (min-width: $breakpointMValue) and (max-width: 1146px) {
      h2 {
        font-size: 18px;
      }
    }
  }
  .everywhere-txt {
    text-align: center;
    margin-top: 30px;
    position: relative;
    h2 {
      margin-left: 10px;
      letter-spacing: 5px;
    }
    @media screen and (min-height: 490px) {
      margin-top: 41px;
    }
    @include media-query(lg) {
      padding-left: 44px;
      margin-top: 0px;
      text-align: left;
      margin-left: 0px;
    }
    @include media-query(xlg) {
      padding-left: 40px;
    }
    @media screen and (max-width: 1288px) and (min-width: 1147px) {
      h2 {
        font-size: 22px;
      }
    }
    @media screen and (min-width: $breakpointMValue) and (max-width: 1146px) {
      h2 {
        font-size: 18px;
      }
    }
  }
  div.background-gradient {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 80%;
    background: url('/static/images/gradient.png') center center repeat;
    background-size: 100% 100%;
    pointer-events: none;
    left: 50%;
    margin-left: -50vw;
    @include media-query(lg) {
      height: 50%;
      z-index: -1;
    }
  }
  div.scroll-down {
    position: absolute;
    font-size: 8px;
    letter-spacing: 0px;
    font-weight: 700;
    text-transform: uppercase;
    color: $white;
    height: 60px;
    margin-top: 0px;
    z-index: 10000;
    bottom: 60px;
    @media screen and (min-height: 580px) {
      bottom: 100px;
    }
    @include media-query(md) {
      // bottom: 0;
      top: auto;
      font-size: 9px;
      letter-spacing: 1px;
      height: 90px;
    }
    @include media-query(lg) {
      position: absolute;
      bottom: 30px;
      left: 50%;
      margin-left: -38px;
      margin-top: 0;
      top: auto;
    }
    div.arrow {
      position: absolute;
      width: 13px;
      height: 37px;
      left: 50%;
      margin-left: -7px;
      margin-top: 0px;
      transform: scale(0.5);
      @include media-query(md) {
        transform: scale(1);
        margin-top: 30px;
      }
      div.center {
        width: 2px;
        height: 37px;
        position: absolute;
        left: 5px;
        background: $white;
      }
      div.left, div.right {
        width: 10px;
        height: 2px;
        background: $white;
        position: absolute;
        bottom: 0;
      }
      div.left {
        transform-origin: center right;
        left: -4px;
        transform: rotate(65deg);
      }
      div.right {
        left: 6px;
        transform-origin: center left;
        transform: rotate(-65deg);
      }
    }
  }
}
</style>



