<template>
  <div class="background">
    <div class="stars" ref="stars" />
    <div class="globe-parallax" ref="globeParallax">
      <div class="globe-container" ref="globeContainer">
        <div class="halo" ref="halo" :style="{ top: `${this.globeTop}px` }" />
        <div class="globe" ref="globe" :style="{ top: `${this.globeTop}px` }">
          <div class="black-over" ref="blackOver" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { TweenMax, Power4, Linear } from 'gsap/all'

export default {
  data () {
    return {
      globeTop: 0
    }
  },
  methods: {
    onScrollWindow () {
      if (window.blockScroll) {
        return
      }
      let posY = window.scrollY || window.scrollYOffset || 0
      if (posY < 0) {
        posY = 0
      }
      let scale = 1 - (posY / 10000)
      if (isNaN(scale)) {
        scale = 1
      }
      const opc = Math.max(0.3, 1 - (posY / 1000))
      TweenMax.killTweensOf(this.$refs.globeContainer)
      TweenMax.killTweensOf(this.$refs.halo)
      TweenMax.to(this.$refs.globeContainer, 1.5, { y: -posY / (window.innerWidth < 1024 ? 4 : 5), ease: Power4.easeOut, scaleX: scale, scaleY: scale, force3D: true })
      TweenMax.to(this.$refs.halo, 3, { opacity: opc, scaleX: scale, scaleY: scale, ease: Power4.easeOut, force3D: true })
    },
    onMouseMove (event) {
      const coefX = (event.clientX - (window.innerWidth / 2)) / window.innerWidth
      const coefY = (event.clientY - (window.innerHeight / 2)) / window.innerHeight
      TweenMax.to(this.$refs.stars, 6, { x: -coefX * 15, y: -coefY * 15, ease: Power4.easeOut })
      TweenMax.to(this.$refs.globeParallax, 4, { x: coefX * 18, y: coefY * 18, ease: Power4.easeOut })
    },
    onResize () {
      const baseElement = document.querySelector(window.innerWidth <= 1024 ? '.phone' : '.image-txt')
      if (baseElement === null) {
        return
      }
      const center = baseElement.getBoundingClientRect()
      const globeRect = this.$refs.globe.getBoundingClientRect().height
      let base = center.top + (center.height / (window.innerWidth <= 1024 ? 3 : 1)) + 30
      if (window.innerWidth < 1024) {
        base = window.screen.height * 0.6
        base -= (window.innerWidth * 1.8) * 0.5
      } else {
        base += (window.scrollY || 0)
        base -= globeRect * 0.26
      }
      this.globeTop = base
    }
  },
  mounted () {
    TweenMax.fromTo(this.$refs.globe, 6, { y: 60 }, { y: 0, ease: Power4.easeOut })
    TweenMax.to(this.$refs.stars, 6, { scaleX: 1.1, scaleY: 1.1, ease: Power4.easeOut })
    TweenMax.fromTo(this.$refs.halo, 12, { y: 100, opacity: 0 }, { y: 0, opacity: 1, delay: 2, ease: Power4.easeOut })
    TweenMax.fromTo(this.$refs.blackOver, 5, { opacity: 1 }, { opacity: 0, delay: 2 })
    TweenMax.fromTo(this.$el, 3, { opacity: 0 }, { opacity: 1, ease: Linear.easeNone })
    window.addEventListener('scroll', this.onScrollWindow)
    window.addEventListener('mousemove', this.onMouseMove)
    window.addEventListener('resize', this.onResize)
    // this.onResize()
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.onScrollWindow)
    window.removeEventListener('mousemove', this.onMouseMove)
    window.removeEventListener('resize', this.onResize)
  }
}
</script>

<style lang="scss">
div.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;

  $globeWidth: 90vw;
  $globeLeft: 10vw;
  $globeTop: 6.3vh;

  $globeWidthMobile: 180vw;
  $globeLeftMobile: -30vw;
  div.stars {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('/static/images/bg-space.jpg') center center repeat;
  }
  div.globe {
    position: absolute;
    top: 0;
    left: 0;
    background: url('/static/images/globe.png') center center no-repeat;
    background-size: cover;
    left: $globeLeftMobile;
    width: $globeWidthMobile;
    height:($globeWidthMobile * 1.22);
    @include media-query(lg) {
      left: $globeLeft;
      width: $globeWidth;
      height:($globeWidth * 1.22);
      background: url('/static/images/globe@2x.png') center center no-repeat;
      background-size: cover;
    }
    div.black-over {
      position: absolute;
      background: $black;
      width: 84.9%;
      height: 69.5%;
      top: 26.85%;
      left: 1.65%;
      border-radius: 50%;
      filter: blur(5px);
    }
  }
  div.halo {
    position: absolute;
    top: 0;
    left: 0;
    background: url('/static/images/halo.png') center center no-repeat;
    background-size: cover;
    left: $globeLeftMobile;
    width: $globeWidthMobile;
    height:($globeWidthMobile * 1.22);
    @include media-query(lg) {
      left: $globeLeft;
      width: $globeWidth;
      height:($globeWidth * 1.22);
      background: url('/static/images/halo@2x.png') center center no-repeat;
      background-size: cover;
    }
    // top: $globeTop;
  }
}
</style>



