<template>
<Intersect @enter="this.onEnter" :threshold="[windowWidth < 1024 ? 0.1 : 0.5]" @leave="this.onLeave">
  <section class="row press">
    <h2 class="double-space blue">LATEST PRESS</h2>
    <p class="sourseTitle">AdAge</p>
    <p class="sourseSubTitle">Pennywise Loves Your Selfies in ‘IT: Chapter 2’ AR Experience. <a href="" target="">[link]</a></p>
    <!-- <Canvas-video
      :src="'static/video/video.mp4'"
      :autoplay="true"
      :loop="true"
      :controls="true"
    ></Canvas-video> -->
    <img class="simpleimage" src="static/images/simpleimage.png" alt="" />
  </section>
</Intersect>
</template>

<script>
import { TweenMax, Power4 } from 'gsap/all'
import Intersect from 'vue-intersect'

export default {
  components: {
    Intersect
  },
  computed: {
    windowWidth () {
      return window.innerWidth
    }
  },
  methods: {
    onEnter () {
      // this.$el.style.visibility = 'visible'
      TweenMax.to(this.$el.querySelector('h2'), 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut })
      TweenMax.staggerTo(this.$el.querySelectorAll('p'), 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut }, 0.1)
      TweenMax.staggerTo(this.$el.querySelectorAll('img'), 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut }, 0.1)
    },
    onLeave () {
      // this.$el.style.visibility = 'hidden'
    }
  },
  mounted () {
    TweenMax.set(this.$el.querySelector('h2'), { y: 30, opacity: 0 })
    TweenMax.set(this.$el.querySelectorAll('p'), { y: 30, opacity: 0 })
    TweenMax.set(this.$el.querySelectorAll('img'), { y: 30, opacity: 0 })
  }
}
</script>

<style lang="scss">
.press {
  position: relative;
  padding-top: 50px;
  padding-bottom: 20px;
  &:before {
    content: "";
    background: $transparentBlack;
    position: absolute;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 50%;
    margin-left: -50vw;
  }
  @include media-query(lg) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    min-height: 900px;
  }
  .sourseTitle {
    line-height: 40px;
    font-size: 40px;
    margin: 30px auto 10px;
    width: 70%;
    max-width: 750px;
  }
  .sourseSubTitle {
    line-height: 40px;
    font-size: 22px;
    width: 80%;
    max-width: 900px;
    margin: 0 auto;

    a {
      font-size: 22px;
    }
  }
  .simpleimage {
    width: 275px;
    width: auto;
  }
}
</style>



