<template>
<Intersect @enter="this.onEnter" :threshold="[windowWidth < 1024 ? 0.1 : 0.5]" @leave="this.onLeave">
  <section class="row team">
    <h2 class="double-space blue">TEAM</h2>
    <ul class="team-list col-xlg-12">
      <li class="col-lg-4 col-md-6">
        <img src="/static/images/andrew.jpg" />
        <h3>ANDREW ROGAL</h3>
        <strong>Exec Chairman Of The Board</strong>
        <p>Has led a number of private technology business from start-up to exit.</p>
      </li>
      <li class="col-lg-4 col-md-6">
        <img src="/static/images/sandro.jpg" />
        <h3>SANDRO CHRISTEN</h3>
        <strong>CEO</strong>
        <p>Over 15 years in entertainment and media space including startup businesses in gaming and sports marketing. Executive management experience in Americas, Europe and Asia.</p>
      </li>
      <li class="col-lg-4 col-md-6">
        <img src="/static/images/alex.jpg" />
        <h3>ALEX GOLDBERG</h3>
        <strong>CTO</strong>
        <p>Experienced technology executive at Microsoft, JPL NASA as well as several startups and venture firms. Alex has developed numerous patents and IP in computer vision, augmented reality and AI.</p>
      </li>
      <li class="col-lg-4 col-md-6">
        <img src="/static/images/sturgis.jpg" />
        <h3>STURGIS ADAMS</h3>
        <strong>CCO</strong>
        <p>Over 20 years in digital marketing and creative direction and strategy for a number of startups, global brands and award-winning technology agencies.</p>
      </li>
      <li class="col-lg-4 col-md-6">
        <img src="/static/images/adam.png" />
        <h3>ADAM BRENNER</h3>
        <strong>VP of Product</strong>
        <p>Over 10 years of experience levaraging emerging technologies to grow brands and build products.</p>
      </li>
      <li class="col-lg-4 col-md-6">
        <img src="/static/images/philipp.jpg" />
        <h3>PHILIPP HOLZSCHNEIDER</h3>
        <strong>Co-Founder & Inventor</strong>
        <p>Expert in computer vision video capture, augmented reality, and drone video camera recognition.</p>
      </li>
    </ul>
  </section>
</Intersect>
</template>

<script>
import { TweenMax, Power4 } from 'gsap/all'
import Intersect from 'vue-intersect'

export default {
  components: {
    Intersect
  },
  computed: {
    windowWidth () {
      return window.innerWidth
    }
  },
  methods: {
    onEnter () {
      // this.$el.style.visibility = 'visible'
      TweenMax.to(this.$el.querySelector('h2'), 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut })
      TweenMax.staggerTo(this.$el.querySelectorAll('ul li'), 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut }, 0.1)
    },
    onLeave () {
      // this.$el.style.visibility = 'hidden'
    }
  },
  mounted () {
    TweenMax.set(this.$el.querySelector('h2'), { y: 30, opacity: 0 })
    TweenMax.set(this.$el.querySelectorAll('ul li'), { y: 30, opacity: 0 })
  }
}
</script>

<style lang="scss">
.team {
  position: relative;
  padding-top: 110px;
  padding-bottom: 103px;
  &:before {
    content: "";
    background: $transparentBlack;
    position: absolute;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 50%;
    margin-left: -50vw;
  }
  h2 {
    margin-bottom: 50px;
  }
  @include media-query(lg) {
    padding-top: 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    min-height: 900px;
  }
  ul.team-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
    max-width: 1440px;
    li {
      padding: 0px 40px;
      margin-bottom: 60px;
      @include media-query(lg) {
        margin-bottom: 20px;
      }
      img {
        width: 130px;
        margin-bottom: 16px;
        height: 130px;
        overflow: hidden;
        border-radius: 50%;
      }
      h3 {
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 8px;
      }
      strong, p {
        font-size: 12px;
        color: $gray;
        line-height: 20.5px;
        max-width: 440px;
        margin: auto;
        width: 60%;
      }
      strong {
        color: $blue;
      }
    }
  }
}
</style>



