<template>
  <div id="app">
    <Background v-if="siteLoaded" ref="background" />
    <router-view v-if="siteLoaded" :key="$route.name" />
    <Preloader v-if="!this.siteLoaded" @loadComplete="siteLoaded = true" />
    <transition name="fade-section" :duration="{ enter: 500, leave: 500}" mode="out-in">
      <div class="header-back row">
        <router-link to="/" v-if="$route.path === '/privacy-policy/' && siteLoaded"><i v-html="arrowSvg" />Back</router-link>
      </div>
    </transition>
    <h1 v-if="siteLoaded" ref="logo"><router-link to="/">Genee</router-link></h1>
  </div>
</template>

<script>
import { TweenMax, Power4 } from 'gsap/all'
import Background from '@/components/background/Background'
import Preloader from '@/components/Preloader'
import arrow from '@/assets/arrow.svg'

export default {
  name: 'App',
  data () {
    return {
      siteLoaded: false
    }
  },
  components: {
    Background,
    Preloader
  },
  computed: {
    arrowSvg () {
      return arrow
    }
  },
  updated () {
    if (this.$refs.logo && !this.logoAnimated) {
      this.logoAnimated = true
      const diff = window.logoRect.top - this.$refs.logo.getBoundingClientRect().top
      TweenMax.from(this.$refs.logo, 3, { y: diff, force3D: true, ease: Power4.easeInOut })
      this.$refs.background.onResize()
    }
  },
  mounted () {
    this.logoAnimated = false
  }
}
</script>
<style lang="scss">
@import '~@/styles/base.scss';
body {
  // overflow: hidden;
  overflow-y: scroll;
  overflow-x: hidden;
}
#app {
  font-family: 'Poppins', Helvetica, Arial, sans-serif;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: $white;
  font-size: 16px;
  min-height: 100vh;
}
html, body {
  overscroll-behavior: none;
}
h1 {
  font-size: 0;
  background: url('/static/images/logo.png') center center no-repeat;
  background-size: cover;
  position: fixed;
  top: 20px;
  left: 50%;
  $logoWidth: 110px;
  width: $logoWidth;
  height: $logoWidth * 0.15;
  margin-left: -($logoWidth / 2);
  a {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
  }
  @include media-query(lg) {
    top: 45px;
    width: 159px;
    height: 25px;
    margin-left: -80px;
  }
  @include media-query(xlg) {
    top: 45px;
  }
}
div.header-back {
  position: fixed;
  top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  a {
    position: relative;
    display: block;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: $white;
    float: left;
    i {
      width: 6px;
      display: inline-block;
      transform: scale(-1, 1);
      margin-right: 5px;
      svg {
        width: 100%;
      }
    }
    &:after {
        content: "";
        width: 100%;
        height: 1px;
        background: $white;
        display: block;
        position: absolute;
        bottom: 0;
        margin-bottom: 0px;
        transition: transform 0.5s $easeOutQuint;
        transform-origin: top right;
        transform: scale(0, 1);
        margin-left: -1px;
      }
      &:hover, &.router-link-exact {
        &:after {
          transform-origin: top left;
          transform: scale(1, 1);
        }
      }
      &.router-link-exact {
        pointer-events: none;
      }
  }
  height: 18px;
  width: 100%;
  @include media-query(lg) {
    top: 45px;
    height: 25px;
  }
  @include media-query(xlg) {
    top: 45px;
  }
}

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.fade-section-enter-active, .fade-section-leave-active {
  transition: all 0.5s linear;
  pointer-events: none;
}
.fade-section-enter, .fade-section-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



