<template>
  <Intersect @enter="this.onEnter" :threshold="[windowWidth < 1024 ? 0.1 : 0.5]">
    <section class="row product">
      <h2 class="blue double-space col-md-6">{{ this.sectionTitle }}</h2>
      <p class="subtitle">{{ this.sectionSubTitle }}</p>
      <ul class="features desktop">
        <li class="core col-lg-3 col-lg-offset-0-half">
          <h3>{{ this.itemTitle1 }}<br class="xlg" /></h3>
          <span v-html="this.itemSubTitle1"><br class="xlg" /></span>
        </li>
        <li class="spacer col-lg-offset-0-half" />
        <li class="mobile-item col-lg-3 col-lg-offset-0-half">
          <h3>{{ this.itemTitle2 }}<br class="xlg" /></h3>
          <span v-html="this.itemSubTitle2"><br class="xlg" /></span>
        </li>
        <li class="spacer col-lg-offset-0-half" />
        <li class="ar col-lg-3 col-lg-offset-0-half">
          <h3>{{ this.itemTitle3 }}<br class="xlg" /></h3>
          <span v-html="this.itemSubTitle3"><br class="xlg" /></span>
        </li>
      </ul>
      <ul class="features mobile features-mobile" ref="mobile" @touchstart="this.onTouchStartCarousel" @mousedown="this.onTouchStartCarousel">
        <li class="core">
          <h3>{{ this.itemTitle1 }}</h3>
          <span v-html="this.itemSubTitle1"></span>
        </li>
        <li class="mobile-item">
          <h3>{{ this.itemTitle2 }}<br /></h3>
          <span v-html="this.itemSubTitle2"></span>
        </li>
        <li class="ar">
          <h3>{{ this.itemTitle3 }}<br /></h3>
          <span v-html="this.itemSubTitle3"></span>
        </li>
      </ul>
      <a href="#" v-html="arrowSvg" class="arrow arrow-next mobile" :class="{ disabled: this.currentAlignedItem === 2 }" @click="onClickArrow(1, $event)" />
      <a href="#" v-html="arrowSvg" class="arrow arrow-prev mobile" :class="{ disabled: this.currentAlignedItem === 0 }" @click="onClickArrow(-1, $event)" />
      <a href="mailto:contact@geenee.me" class="contact">{{ this.contactButton }}</a>
    </section>
  </Intersect>
</template>

<script>
import { TweenMax, Power4, Back } from 'gsap/all'
import Intersect from 'vue-intersect'
import arrow from '@/assets/arrow.svg'

export default {
  props: [
    'sectionTitle',
    'sectionSubTitle',
    'contactButton',
    'itemTitle1',
    'itemTitle2',
    'itemTitle3',
    'itemSubTitle1',
    'itemSubTitle2',
    'itemSubTitle3'
  ],
  filters: {
    capitalize: function (value) {
      value.split('\n')
      value = value.toString()
      return value.charAt(0).toUpperCase() + value.slice(1)
    }
  },
  data () {
    return {
      currentAlignedItem: 0,
      iniCurrX: 0,
      iniAlignedItem: 0,
      iniTransformX: 0,
      transformX: 0
    }
  },
  components: {
    Intersect
  },
  computed: {
    arrowSvg () {
      return arrow
    },
    windowWidth () {
      return window.innerWidth
    }
  },
  methods: {
    onEnter () {
      TweenMax.staggerTo(this.$el.children, 2, { y: 0, opacity: 1, force3D: true, ease: Power4.easeOut }, 0.2)
    },
    onClickArrow (coef, event) {
      event.preventDefault()
      this.alignToItem(this.currentAlignedItem + coef)
    },
    onTouchStartCarousel (event) {
      this.iniCurrX = event.clientX ? event.clientX : event.touches[0].clientX
      this.iniTransformX = this.transformX
      this.iniCurrY = event.clientY ? event.clientY : event.touches[0].clientY
      if ('ontouchstart' in window) {
        window.addEventListener('touchmove', this.onTouchMove)
        window.addEventListener('touchend', this.onTouchEnd)
        window.addEventListener('touchcancel', this.onTouchEnd)
      } else {
        window.addEventListener('mousemove', this.onTouchMove)
        window.addEventListener('mouseup', this.onTouchEnd)
      }
      // window.blockScroll = true
      this.iniTime = new Date().getTime()
    },
    onTouchMove (event) {
      const diffX = event.clientX ? event.clientX - this.iniCurrX : event.touches[0].clientX - this.iniCurrX
      const diffY = event.clientY ? event.clientY - this.iniCurrY : event.touches[0].clientY - this.iniCurrY
      if (Math.abs(diffY) > 150) {
        window.blockScroll = false
      }
      this.transformX = this.iniTransformX + (diffX * 1.1)
      this.lastDiffX = diffX
      TweenMax.to(this.$refs.mobile.children, 0.4, { x: this.transformX, opacity: 1, force3D: true, ease: Power4.easeOut })
    },
    onTouchEnd (event) {
      window.removeEventListener('touchmove', this.onTouchMove)
      window.removeEventListener('touchend', this.onTouchEnd)
      window.removeEventListener('touchcancel', this.onTouchEnd)
      window.removeEventListener('mousemove', this.onTouchMove)
      window.removeEventListener('mouseup', this.onTouchEnd)
      window.blockScroll = false
      const diffTime = new Date().getTime() - this.iniTime
      if (diffTime < 150) {
        if (this.lastDiffX > 0) {
          this.alignToItem(Math.max(0, this.currentAlignedItem - 1))
        } else {
          this.alignToItem(Math.min(2, this.currentAlignedItem + 1))
        }
      } else {
        this.alignToNearest()
      }
    },
    alignToNearest () {
      const carouselWidth = this.$refs.mobile.getBoundingClientRect().width
      let nearest = -Math.round(this.transformX / (carouselWidth * 1.3))
      if (nearest < 0) {
        nearest = 0
      } else if (nearest > 2) {
        nearest = 2
      }
      if (carouselWidth === 0) {
        nearest = 0
      }
      this.currentAlignedItem = nearest
      this.transformX = -(nearest * carouselWidth * 1.3)
      TweenMax.killTweensOf(this.$refs.mobile)
      TweenMax.to(this.$refs.mobile.children, 0.4, { x: this.transformX, opacity: 1, force3D: true, ease: Back.easeOut })
    },
    alignToItem (index) {
      const carouselWidth = this.$refs.mobile.getBoundingClientRect().width
      this.currentAlignedItem = index
      this.transformX = -(this.currentAlignedItem * carouselWidth * 1.3)
      TweenMax.to(this.$refs.mobile.children, 0.4, { x: this.transformX, opacity: 1, force3D: true, ease: Back.easeOut })
    },
    onResizeWindow () {
      this.alignToNearest()
    }
  },
  mounted () {
    window.addEventListener('resize', this.onResizeWindow)
    this.onResizeWindow()
    TweenMax.set(this.$el.children, { y: 30, opacity: 0 })
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResizeWindow)
  }
}
</script>

<style lang="scss">
section.product {
  height: 100vh;
  &:before {
    content: "";
    background: $transparentBlack;
    position: absolute;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 50%;
    margin-left: -50vw;
  }
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  padding-top: 100px;
  padding-bottom: 60px;
  position: relative;
  @include media-query(sm) {
    min-height: 800px;
  }
  @include media-query(md) {
    height: 100vh;
    min-height: 900px;
    padding-top: 125px;
    padding-bottom: 103px;
  }
  h2 {
    width: 100%;
    margin-bottom: 65px;
  }
  p.subtitle {
    line-height: 50px;
    font-size: 25px;
    margin: 0 0 30px;
    width: 70%;
    max-width: 750px;
    margin: 0 auto;
  }
  ul.features {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    margin-top: 58px;
    min-height: 240px;
    li {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      &.spacer {
        width: 1px;
        height: 100%;
        background: $white;
        opacity: 0.2;
      }
      span {
        color: $blue;
        font-size: 21px;
        line-height: 36px;
        margin-top: 50px;
        flex-grow: 0;
        flex-shrink: 0;
        max-width: 315px;
      }
      strong, p {
        font-size: 14px;
        color: $gray;
        line-height: 22.5px;
      }
      strong {
        margin-top: 34px;
      }
      div.text-lines {
        flex-grow: 1;
        justify-content: space-between;
        display: flex;
        flex-direction: column;
        margin-top: 28px;
      }
      h3 {
        font-weight: 700;
        text-transform: uppercase;
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 21px;
        text-align: left;
        flex-grow: 0;
        flex-shrink: 0;
        div.icon {
          // width: auto;
          display: block;
          svg {
            height: 45px;
            width: auto;
          }
          &.core {
            margin-right: 25px;
          }
          &.mobile-item {
            margin-right: 15px;
          }
          &.ar {
            margin-right: 28px;
          }
        }
      }
    }
  }

  ul.features-mobile {
    margin-top: 70px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    cursor: pointer;
    user-select: none;
    background: rgba(0, 0, 0, 0.1);
    overflow-x: hidden;
    li.spacer {
      display: none;
    }
    li {
      width: 130% !important;
      flex-basis: 130%;
      flex-grow: 1;
      flex-shrink: 0;
      position: absolute;
      padding: 0 15%;
      height: 100%;
      margin-left: -15%;
      &:nth-child(2) {
        margin-left: 115%;
      }
      &:nth-child(3) {
        margin-left: 245%;
      }
    }
  }
  a.contact {
    flex-grow: 0;
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.35px;
    padding: 17px 0px;
    border: 1px solid $white;
    position: relative;
    display: block;
    width: 320px;
    margin: 0 auto;
    margin-top: 70px;
    transition: all 2s $easeOutQuint;
    @include media-query(sm) {
      font-size: 12px;
      width: 260px;
    }
    @include media-query(md) {
      width: 350px;
    }
    &:before {
      position: absolute;
      width: 100%;
      height: 100%;
      background: $blue;
      z-index: -1;
      content: "";
      top: 0;
      left: 0;
      transform: scale(1, 1);
      opacity: 0;
      transition: all 2s $easeOutQuint;
    }
    &:hover {
      letter-spacing: 2px;
      color: $white;
      &:before {
        opacity: 0.5;
        transform: scale(1, 1);
      }
    }
  }
  a.arrow {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 50%;
    &.disabled {
      opacity: 0.3 !important;
      pointer-events: none;
    }
    svg {
      width: 13px;
      position: absolute;
      left: 20px;
      top: 20px;
      margin-top: -13px;
      margin-left: -6px;
    }
    &.arrow-next {
      right: 10px;
    }
    &.arrow-prev {
      svg {
        transform: scaleX(-1);
        left: 0;
      }
    }
  }
}
</style>



